'use client'
import { useDropzone } from 'react-dropzone';

export default function HeroSection() {
  const { getRootProps, getInputProps } = useDropzone({
    accept: {
      'video/mp4': ['.mp4'], // Accepts .mp4 files
    },
    onDrop: (acceptedFiles) => {
      console.log(acceptedFiles); // Handle the dropped files here
    },
  });

  return (
    <section className="relative w-full min-h-screen text-white overflow-hidden bg-gradient-to-b dark:from-black dark:to-gray-800 from-white to-gray-200">
      {/* Background Image with a smooth blur */}
      <div className="absolute inset-0 bg-cover bg-center blur-md" style={{ backgroundImage: 'url(/hero-image.jpg)' }}></div>
      
      {/* Hero Content */}
      <div className="absolute inset-0 flex items-center justify-center flex-col space-y-6 text-center z-10 px-4 sm:px-12">
        <h1 className="text-5xl sm:text-6xl font-semibold tracking-tight text-white animate__animated animate__fadeIn animate__delay-1s">
          Convert MP4 to MP3 with Ease
        </h1>
        <p className="text-lg sm:text-xl text-gray-200 animate__animated animate__fadeIn animate__delay-2s">
          Fast, secure, and high-quality MP4 to MP3 conversion without the need to sign up. Simple, seamless, and user-friendly.
        </p>

        {/* DropZone */}
        <div
          {...getRootProps()}
          className="bg-white bg-opacity-20 border-4 border-white border-dashed rounded-lg p-12 text-white w-full sm:w-96 mx-auto transition-all duration-300 hover:bg-opacity-40 hover:scale-105 transform"
        >
          <input {...getInputProps()} />
          <p className="text-lg">Drag and drop your MP4 file here or click to select</p>
        </div>

        {/* Call-to-Action buttons */}
        <div className="space-x-6">
          <a
            href="#"
            className="bg-black text-white py-3 px-8 rounded-full text-xl hover:bg-opacity-80 transition-all duration-300 transform hover:scale-105 animate__animated animate__fadeIn animate__delay-3s"
          >
            Convert Now
          </a>
          <a
            href="#"
            className="bg-transparent border-2 border-white py-3 px-8 rounded-full text-xl text-white hover:bg-opacity-10 hover:border-opacity-80 transition-all duration-300 transform hover:scale-105 animate__animated animate__fadeIn animate__delay-4s"
          >
            Learn More
          </a>
        </div>
      </div>
    </section>
  );
}
